import React, { Component } from 'react';

import queryString from 'query-string';
import withAuthorization from '../components/Session/withAuthorization';
import { firebase, db, auth } from '../firebase';
import AuthUserContext from '../components/Session/AuthUserContext';
import Layout from '../components/layout';

class ListPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      listId: queryString.parse(this.props.location.search).id || "",
      users: null,
      currentUserId: '',
      joinButton: false,
      newListName: '',
      nameChanged: false,
      isDraw: false,
      errorMessage: '',
    };
  }

  componentDidMount() {
    if (typeof window !== 'undefined') {
      firebase.auth.onAuthStateChanged(authUser => {
        if (authUser) {
          this.setState({currentUserId: auth.getUserId()});
          db.readList(this.state.listId).then(snapshot => {
            if(!snapshot.isDraw) {
              //isn't draw
              this.setState({users: snapshot});
              this.setState({newListName: snapshot.name});

              //join button if not on a list
              let tempJoinButton = true;
              snapshot.userList.forEach((user) => {
                if(user.userId === this.state.currentUserId) {
                  tempJoinButton = false;
                }
              })
              this.setState({joinButton: tempJoinButton})
            } else {
              //is already isDraw
              this.setState({isDraw: snapshot.isDraw})
              this.setState({newListName: snapshot.name});
              snapshot.userList.forEach((user) => {
                if(user.userId === this.state.currentUserId) {
                  this.setState({myDraw: user.userDraw})
                }
              })
            }
          });
        }
      });
    }

  }

  render() {
    const { isDraw, myDraw, users, listId, joinButton, newListName, nameChanged, errorMessage } = this.state;

    const onNameChange = (e) => {
      this.setState({newListName: e.target.value, errorMessage: ''})
    }

    const onClick = () => {
      if(newListName !== '') {
        db.changeListName(listId, newListName).then(()=> {
          this.setState({nameChanged: true});
          setTimeout(() => {
            this.setState({nameChanged: false});
          }, 3000)
        });
      } else {
        this.setState({errorMessage: 'list name cannot be empty'});
      }
    }

    const onDeleteUser = (userIndex, userId, name) => {
      const confirm = window.confirm(`do you want delete user ${name}?`);
      if(confirm) {
        db.deleteUserFromList(listId, userId, userIndex).then(()=> {window.location.reload()});
      }
    }

    return (
      <Layout>
        <h1>List</h1>
        <input readOnly value={this.props.location.href}/>
        {!isDraw &&
          <ListPageContent
          users={users}
          listId={listId}
          joinButton={joinButton}
          newListName={newListName}
          onNameChange={onNameChange}
          onClick={onClick}
          nameChanged={nameChanged}
          onDeleteUser={onDeleteUser}
          errorMessage={errorMessage}
          />
        }
        {isDraw &&
         <div>
         <h2>{newListName}</h2>
         My person is: {myDraw}
         </div>}
      </Layout>
    );
  }
}

const authCondition = authUser => !!authUser;

const ListPageContent = withAuthorization(authCondition)(({users, listId, joinButton, newListName, onNameChange, onClick, nameChanged, onDeleteUser, location, errorMessage}) => (
  <div>
      {joinButton && <AuthUserContext.Consumer>
        {authUser => (
          <div>
            <button onClick={() => db.joinList(listId).then(()=> {window.location.reload()})}>Join list</button>
          </div>
        )}
      </AuthUserContext.Consumer>}
      {!joinButton && !!users && <div>
        <input defaultValue={users.name} value={newListName} onChange={onNameChange}/>
        {errorMessage && <div>{errorMessage}</div>}
        <button onClick={onClick} disabled={errorMessage || newListName===''}>change name</button>
        {nameChanged && <div> Name was changed!!!</div>}
      </div>}
      {!joinButton && users && users.userList && users.userList.length > 2 &&<button onClick={() => db.draw(listId).then(()=> {window.location.reload()})}>Draw</button>}
      <ul>
      {users && users.userList && (users.userList).map((element, index)=> (
          <li key={index}>
          {element.userName}
          {!joinButton && <button onClick={() => onDeleteUser(element.index, element.userId, element.userName)}>delete user</button>}
          </li>
      ))}
      </ul>
  </div>
));

export default ListPage;
